.js-container .card {
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 15px;
  transition: transform .2s ease-in-out;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

.js-container .card:hover {
  transform: translateY(-5px);
}

.title {
  margin-bottom: 10px;
  font-size: 1.5rem;
  font-weight: bold;
}

.parag {
  color: #555;
  margin: 5px 0;
  font-size: 1rem;
}

.weather-widget {
  max-width: 300px;
  color: #333;
  background-color: #f0f8ff;
  border-radius: 10px;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
  font-size: 16px;
  list-style: none;
  display: grid;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.weather-item {
  text-align: center;
  background-color: #fff;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  padding: 10px;
  font-weight: bold;
  display: flex;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.weather-country, .weather-capital, .weather-temp, .weather-humidity {
  grid-column: span 1;
}

.weather-condition {
  background-color: #fff;
  border-radius: 8px;
  grid-column: 1 / span 2;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 10px;
  list-style: none;
  display: flex;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.weather-condition-text {
  color: #333;
  margin: 0;
  font-size: 14px;
}

.weather-icon {
  width: 60px;
  height: auto;
}

.weather-item:hover {
  cursor: pointer;
  background-color: #e0f7fa;
}

